html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    background: lightgrey;
}

#browse-sort-button, #browse-submit-button {
    text-decoration: none;
    color: black;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    cursor: pointer;
    font-family: Raleway, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin-bottom: 2rem;
    box-shadow: none;
    font-weight: bold;
}

#browse-submit-button {
    margin-top: 1rem;
}

#browse-sort-button:hover, #browse-submit-button:hover {
    color: white;
    background: black;
    transition: 0.25s;
}

#sort-select, #browse-unit-select {
    width: 20rem;
    font-family: Raleway, Arial, Helvetica, sans-serif;
    font-size: 1.8rem;
    margin: 0 auto;
    cursor: pointer;
}

.browse-from-ago {
    text-align: center;
    font-family: Raleway, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 2rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-weight: bold;
}

#browse-div-nav {
    margin: 1.5rem auto;
    display: flex;
    justify-content: space-evenly;
}

.browse-arrows {
    font-size: 2.4rem;
    font-weight: bold;
    cursor: pointer;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.browse-arrows:hover{
    color: white;
    transition: 0.25s;
} 

#browse-offset-number {
    font-size: 2.8rem;
    font-family: Raleway, 'Times New Roman', Times, serif;
    font-weight: bold;
}
